import PropTypes from "prop-types";
import { useState } from "react";
import { format } from "date-fns";
// @mui
import { useTheme } from "@mui/material/styles";
import {
  TableRow,
  Checkbox,
  TableCell,
  Typography,
  MenuItem,
} from "@mui/material";
// utils
import { fCurrency, fPercent } from "../../../../utils/formatNumber";
// components
import Iconify from "../../../../components/Iconify";
import { TableMoreMenu } from "../../../../components/table";
import { useTranslation } from "react-i18next";

import useAuth from "../../../../hooks/useAuth";
import handlePermission from "../../../../utils/handlePerm";
import { PERM } from "../../../../guards/perms";

// ----------------------------------------------------------------------

FeeTableRow.propTypes = {
  row: PropTypes.object,
  selected: PropTypes.bool,
  onEditRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onDeleteRow: PropTypes.func,
  deleteText: PropTypes.string,
  editText: PropTypes.string,
};

export default function FeeTableRow({
  row,
  selected,
  onEditRow,
  onSelectRow,
  onDeleteRow,
  deleteText = "Delete",
  editText = "Edit",
}) {
  const theme = useTheme();
  const { t } = useTranslation();
  const { user } = useAuth();

  const permissions_list = user?.user_permissions_list || [];
  const canDelete = handlePermission(permissions_list, PERM.CONFIG_FEE_DELETE);
  const canEdit = handlePermission(permissions_list, PERM.CONFIG_FEE_CHANGE);

  const [openMenu, setOpenMenuActions] = useState(null);

  const handleOpenMenu = (event) => {
    setOpenMenuActions(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpenMenuActions(null);
  };

  return (
    <TableRow hover selected={selected}>
      <TableCell padding="checkbox">
        <Checkbox checked={selected} onClick={onSelectRow} />
      </TableCell>

      <TableCell padding="checkbox" />

      <TableCell align="left">{row.type_transaction}</TableCell>
      <TableCell align="center">{fCurrency(row.minimum_amount)}</TableCell>
      <TableCell align="center">{fCurrency(row.maximum_amount)}</TableCell>
      <TableCell align="center">
        {row.is_percent ? fPercent(row.fee_amount) : fCurrency(row.fee_amount)}
      </TableCell>

      <TableCell align="center">
        <Typography variant="subtitle2">
          {format(new Date(row.last_updated), "dd MMM yyyy")}
        </Typography>
        <Typography variant="body2" sx={{ color: "text.secondary" }}>
          {format(new Date(row.last_updated), "p")}
        </Typography>
      </TableCell>

      <TableCell align="right">
        <TableMoreMenu
          open={openMenu}
          onOpen={handleOpenMenu}
          onClose={handleCloseMenu}
          actions={
            <>
              {canDelete && (
                <MenuItem
                  onClick={() => {
                    handleCloseMenu();
                    onDeleteRow();
                  }}
                  sx={{ color: "error.main" }}
                >
                  <Iconify icon={"eva:trash-2-outline"} />
                  {deleteText}
                </MenuItem>
              )}

              {canEdit && (
                <MenuItem
                  onClick={() => {
                    onEditRow();
                    handleCloseMenu();
                  }}
                >
                  <Iconify icon={"eva:edit-fill"} />
                  {editText}
                </MenuItem>
              )}
            </>
          }
        />
      </TableCell>
    </TableRow>
  );
}
